#cotizador {
  color: #fff;
}

p.small {
  font: 12px;
}
h1.big-title {
  font-size: 48px;
  color: $blue-1;
  font-weight: 800;
}

.title-section {
  font-size: 20px;
  font-weight: 700;
  text-align: center;
}

h2.credit-form-title {
  font-size: 16px;
  padding-top: 10px;
}
.continua-credito span {
  font-size: 18px;
  color: #999;
}

h2.continua-titulo {
    color: #E9B52B;
    padding: 10px 0;
    font-size: 19px;
    text-align: left;
}

h2.continua-titulo::after {
  content: '';
  position: absolute;
  top: 75px;
  left: 42%;
  margin-left: -28%;
  width: 9%;
  height: 1px;
  background: $yellow;
}
.form-group span {
  display: block;
  font-size: 11px;
  padding-left: 5px;
  line-height: 25px;
}

span.input-success {
  color: $blue-1;
}

#preguntas-frecuentes h2 {
  color: #D65D98;
  font-weight: 800;
}

#preguntas-frecuentes h4 {
    font-weight: 800;
    letter-spacing: 1.5px;
    color: #fff;
    text-align: left;
    font-size: 19px;
}

#preguntas-frecuentes p {
    font-weight: 100;
    letter-spacing: 2px;
    color: #fff;
    line-height: 20px;
    text-align: left;
    font-size: 13px;
    padding-top: 12px;
}

#preguntas-frecuentes h4::after {
    content: '';
    position: absolute;
    top: 66px;
    left: 43%;
    margin-left: -14%;
    width: 37%;
    height: 2px;
    background: #D65D98;
}

span.action {
  font-size: 22px;
  color: $yellow;
}

h3.section-header {
  font-size: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid $green-bg;
}

h3.section-header-home {
  font-size: 20px;
  padding: 15px;
  border-left: 1px solid $green-bg;
}

.section-list-title {
  font-size: 12px;
  color: #2B3B46;
  line-height: 16px;
  text-transform: uppercase;
  margin: 10px 0;
}

.section-list-item {
  font-size: 18px;
  color: $blue-1;
  line-height: 24px;
}

h3.section-header-home span {
  font-size: 15px;
  color: $blue-1;
  font-weight: 700;
}

h2.section-h {
  color: $blue-1;
  padding: 30px 0;
  font-size: 24px;
}

h2.section-h:before, h2.section-h:after {
  content: '';
  position: absolute;
  top: 90px;
  left: 42%;
  margin-left: -23%;
  width: 24%;
  height: 1px;
  background: $blue-1;
}

h5.status {
  margin: 45px 0 70px;
  text-decoration: underline;
  font-weight: 800;
}

#typed {
  font-size: 18px;
  color: $blue-1;
  padding-top: 20px;
}

.questions {
  padding: 20px 0;
}

.questions a, .questions a:hover, .questions a:focus {
  color: #333;
}

.testimonios h2.light-title {
	color:#fff;
  padding: 0 0 40px 0;
}

.steps-header {
  padding-top: 40px;
  padding-bottom: 40px;
  color: #FFFFFF !important;

}

.steps-header h2 {
  color: #fff !important;
  font-weight: 900;
}

.steps-header span {
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 100;
}

.step h3 {
  font-size: 24px;
  text-align: left;
}

.steps-title {
  font-size: 18px;
    padding-top: 60px;
    text-align: left;
}

.underline {
    border-bottom: 1px solid #333;

}


.steps-info p {
  font-size: 12px;
  padding: 20px 0;
  color: #999;
  text-align: left;
}

.person img {
  height: 7em;

}

.person p {
  display: inline-block;
  position: relative;
  top: 20px;
  left: 7px;
}

.person span {
  display: block;
  font-size: 12px;
  color: $blue-1;
}

.lead {
    padding: 0;
    margin-bottom: 0;
}

.lead .title-status, .lead .lead-status {
  font-size: 3.4em;
  color: $primary-pink;
}

.lead span {
  font-size: 1.6em;
    color: #fff;
}

.steps-title-status {
  font-size: 2.5em;
  color: $blue-1;
}

h3.oferta-principal {
	font-size: 24px;
	margin-top: 0;
  color: #333;
}

.item-oferta-principal{
	font-size: 36px;
  color: $primary-pink;
}

.item-quincena {
	font-size: 36px;
	color: $primary-pink;
}

h6 {
  color: $blue-1;
}

.title-status {
  display: none;
}
