@import 'partials/variables';
@import 'partials/background';
@import 'partials/fonts';
@import 'partials/nav';
@import 'partials/side_menu';
@import 'partials/icons';
@import 'partials/forms';
@import 'partials/buttons';
@import 'partials/panels';
@import 'partials/slider';
@import 'partials/loader';
@import 'partials/editable';
@import 'partials/lists';
@import 'partials/tables';
@import 'partials/modal';
@import 'partials/progress-wizard';

body {
	background-color: #fff;
	font-family: 'Raleway', sans-serif;
	color: #2B3B46;
	position: relative;
	margin: 0;
	min-height: 100%;
}

.form,
.home {
	padding-top: 60px;
	padding-bottom: 15rem;
}

.status {
	padding-top: 80px;
	padding-bottom: 20rem;
}

.container {
	padding-top: 30px;
}

.footer {
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 0;
	background-color: #fff;
	text-align: center;
	font-size: 18px;
	padding-bottom: 20px;
}

.footer-title {
	padding-bottom: 30px;
}

.footer-title::after {
	content: '';
  position: absolute;
  top: 76px;
	left: 43%;
  margin-left: 5%;
  width: 4%;
  height: 2px;
  background: #D65D98;
}

.footer a {
	color: #333;
}

.center {
	text-align: center;
}

.nav-btn-back {
	color: $blue-1;
	font-size: 1.2em;
}

.nav-btn-back i {
	padding-right: 6px;
	font-size: 1.3em;
	font-weight: 100;
}

.add-on input {
	text-align: center;
	width: 100%;
}

.add-on .input-group-btn {
	position: absolute;
}

.add-on .btn-default {
	border-color: transparent !important;
}

.add-on .btn-default:hover {
	background-color: #fff;
	cursor: pointer;
}

.add-on .input-group-btn.left {
	top: 4px;
	border: 0;
	left: 0;
	z-index: 4;
}

.add-on .input-group-btn.left i {
	color: red;
}

.add-on .input-group-btn.right i {
	color: $blue-1;
}

.add-on .input-group-btn.right {
	top: 4px;
	border: 0;
	right: 35px !important;
	z-index: 4;
}
/* stop the glowing blue shadow */
.add-on .form-control:focus {
	border-color: #cccccc;
}

.form-control[disabled],
.form-control[readonly] {
	background-color: #fff;
}

.credit-proposals-wrapper {
	padding-top: 25px;
}

#pre-autorizacion p {
	margin: 32px 0;
}

.img-title {
	padding-top: 25px;
}

.folio-aut {
	padding-bottom: 25px;
}

.folio-aut img {
	padding-left: 10px;
}

.denegado .title-section {
	padding-bottom: 64px;
}

.dropzone {
	border: 1px dashed $blue-1;
	min-height: 100px;
	border-radius: 8px;
}

.dropzone .dz-message span i {
	position: absolute;
	top: 10px;
	right: 23px;
	font-size: 2em;
	color: $blue-1;
}

.profile-pic {
	height: 80px;
	position: absolute;
	margin: 0 auto;
	border-radius: 50%;
	padding: 0 !important;
	top: -50px;
	left: 35%;
}

#photo-label {
	position: absolute;
	right: 30px;
	top: -2px;
}

.modal {
	margin: 0 auto;
}

.padding-top {
	padding-top: 40px;
}

.menu_credit_type .col-xs-6 {
	min-height: 120px;
}

.footer p {
	color: #333;
}

.footer a:hover {
	color: $blue-1;
}

.testimonios {
	background: $header-bg;
	min-height: 450px;
	padding: 0;
}

.testimonios .container {
	padding: 0;
}

.preguntas-container {
	background: #bababa;
  opacity: 0.9;
  height: 100%;
	margin-top: 50px;
}

.question {
	padding: 5px 40px;
}

.steps-info img {
	height: 70px;
	right: 50px;
	top: 65px;
	position: absolute;
	opacity: 0.5;
}

#cel-credit {
	height: 400px;
	padding-top: 50px;
	text-align: left;
}

blockquote {
	border-left: 0;
	padding: 54px 48px;
}

blockquote::before{
  content: "\201C"; /*Unicode for Left Double Quote*/

  /*Font*/
  font-family: Georgia, serif;
  font-size: 70px;
  font-weight: bold;
  color: #87B944;

  /*Positioning*/
  position: absolute;
  left: 10px;
  top:5px;

}

blockquote::after{
  content: "\201D"; /*Unicode for Right Double Quote*/

  /*Font*/
  font-family: Georgia, serif;
  font-size: 70px;
  font-weight: bold;
  color: #87B944;

  /*Positioning*/
  position: absolute;
  right: 10px;
  top: 105px;

}

#map {
	width: 100%;
	height: 400px;
}

.controls {
	margin-top: 10px;
	border: 1px solid transparent;
	border-radius: 2px 0 0 2px;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	height: 32px;
	outline: none;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

#cotizador {
	min-height: 100vh;
	background-image: url("../img/family.jpg");
	/* Full height */
	height: 100%;
	/* Center and scale the image nicely */
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.testimony-container {
	min-height: 450px;
  padding: 0;
}





#pic-testimony {
    padding-right: 0;
    height: 55%;
    background-image: url(../img/profile.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.testimony-name {
  padding: 5px 12px;
	background: #DDDCDC;
	min-height: 165px;
}

.testimony-name h2 {
	color: #87B944;
	font-size: 18px;
}

.testimony-name h2::after {
	content: '';
  position: absolute;
  top: 50px;
	left: 59%;
  margin-left: -33%;
  width: 21%;
  height: 1px;
  background: #999;
}

.testimony-name span {
	color: #fff;
	font-size: 18px;
}

.testimony-name.active span {
	color: #DDDCDC;
}

.testimony-name.active h2 {
	color: #fff;
}

.testimony-name.active {
	background: #87B944;
}

#cotizador-container {
	background-color: rgba(200, 28, 120, 0.5);
}

#searchInput {
	background-color: #fff;
	font-family: Roboto;
	font-size: 15px;
	font-weight: 300;
	margin-left: 12px;
	padding: 0 11px 0 13px;
	text-overflow: ellipsis;
	width: 50%;
}

#searchInput:focus {
	border-color: #4d90fe;
}

#pasos-credito {
	background: $morado;
	padding-bottom: 70px;
}

.step {
	background: #fff;
	padding: 20px;
	margin: 5px 25px;
}

#preguntas-frecuentes {
	background-image: url("../img/buildings.jpg");
	height: 200vh;
	/* Center and scale the image nicely */
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.float-right {
	float: right;
}
/* importamos responsive al final */
@import 'partials/responsive';
